*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 14px;
  line-height: 1.85714em;
  font-family: 'WorkSans', 'Roboto', 'Arial', sans-serif;
}

.wrapper {
  display: flex;
  align-items: center;
  flex-flow: row wrap;
  max-width: 81.42857em;
  justify-content: space-between;
  padding-right: 1.07143em;
  padding-left: 1.07143em;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 1199px) {
  .wrapper {
    max-width: 68.57143pxem;
  }
}

@media only screen and (max-width: 1023px) {
  .wrapper {
    max-width: 51.42857pxem;
  }
}

@media only screen and (max-width: sizepx) {
  .wrapper {
    max-width: 100%;
  }
}

img {
  max-width: 100%;
}

a {
  color: white;
  position: relative;
  text-decoration: none;
}

a:before {
  width: 0;
  height: 2px;
  content: "";
  display: block;
  position: absolute;
  background-color: #2595fe;
  bottom: -0.71429pxem;
  left: 0;
  transition-duration: .4s;
  transition-property: width;
  transition-timing-function: ease-in-out;
}

a:hover {
  text-decoration: none;
}

a:hover:before {
  width: 100%;
}

ul,
ol {
  padding-left: 0;
  display: flex;
  list-style: none;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  ul,
  ol {
    position: absolute;
    top: 100%;
    left: -100%;
    width: 100%;
    background-color: #1a1a1a;
    flex-flow: column nowrap;
    justify-content: flex-start;
    transition: left .3s ease-in-out;
  }
}

button {
  border: 0;
  cursor: pointer;
  background-color: transparent;
}

button.button {
  background-color: #2595fe;
  border-radius: 4px;
  transition-duration: .4s;
  transition-property: background-color;
  transition-timing-function: ease-in-out;
}

button.button:hover {
  background-color: #218bed;
}

input,
textarea,
select,
button {
  font: inherit;
}

.sr-only {
  border: 0;
  width: 1px;
  padding: 0;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
}

.title {
  margin-bottom: 0;
}

.bg--dark {
  background-color: #242424;
}

header {
  position: relative;
}

@media only screen and (max-width: 767px) {
  header .wrapper {
    padding-top: 1.42857em;
    padding-bottom: 1.42857em;
  }
}

.logo-link {
  color: inherit;
  line-height: 0;
}

.logo-link:before {
  display: none;
}

.logo-link__pic {
  max-width: 13.35714em;
  height: 6.07143em;
}

@media only screen and (max-width: 767px) {
  .logo-link__pic {
    max-width: 8.57143em;
    height: 1.14286em;
  }
}

.nav__burger {
  display: none;
}

@media only screen and (max-width: 767px) {
  .nav__burger {
    display: inline-block;
    position: relative;
    width: 2.14286em;
    height: 1.14286em;
  }
  .nav__burger:before {
    margin-top: -7px;
  }
  .nav__burger:after {
    margin-top: 5px;
  }
  .nav__burger:before, .nav__burger:after {
    content: '';
    position: absolute;
    transition: transform .3s ease-in-out;
  }
  .nav__burger span, .nav__burger:before, .nav__burger:after {
    width: 100%;
    height: 2px;
    display: block;
    background-color: white;
  }
  .nav__burger--active {
    width: 2.14286em;
    height: 1.14286em;
  }
  .nav__burger--active span {
    display: none;
  }
  .nav__burger--active:before {
    transform: rotate(45deg);
  }
  .nav__burger--active:after {
    transform: rotate(-45deg);
  }
  .nav__burger--active:before, .nav__burger--active:after {
    margin-top: 0;
  }
}

.nav__list {
  line-height: 1.85714em;
}

@media only screen and (max-width: 767px) {
  .nav {
    line-height: 0;
  }
}

.list__item {
  font-size: 1.28571em;
}

.list__item:not(:last-child) {
  margin-right: 1.11111em;
}

@media only screen and (max-width: 767px) {
  .list__item {
    width: 100%;
  }
  .list__item:not(:last-child) {
    margin-right: 0;
  }
}

.list__link {
  padding: 0;
}

.list__link:before {
  bottom: -0.71429em;
}

@media only screen and (max-width: 767px) {
  .list__link {
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 0.55556em;
    border: 1px solid #242424;
    transition: background-color .3s ease-in-out;
  }
  .list__link:hover, .list__link--active {
    background-color: #242424;
    border: 1px solid #1a1a1a;
  }
  .list__link:before {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .list--active {
    left: 0;
  }
}
